.modalcard {
  max-width: 100%;
  display: flex;
  border-radius: 8px;
  flex-direction: column;
  padding: 0rem 1rem;
  align-items: center;
  max-height: 85vh;
  overflow-y: auto;
  margin: 2rem 0.5rem;
}

.modalcard__img img {
  height: auto;
  border-radius: 8px 8px 0 0;
  max-width: 580px;
}

.modalcard__props {
  display: flex;
  justify-content: flex-start;
  gap: 2rem;
  margin-bottom: 0;
}

.modalcard__info {
  padding: 1rem 1rem;
  max-width: 100%;
  overflow-x: hidden;
}

.popup.is-active {
  display: flex;
}

.modalcard__close-button {
  margin-left: -2rem;

  & .close-button__img {
    max-width: 1.5rem;
    padding: 0.5rem;
    box-sizing: content-box;
    border-radius: 9px;
    cursor: pointer;
    transition: 0.3s;
  }
  & .close-button__img:hover {
    background-color: rgba(142, 142, 142, 0.8);
  }
}
