.detailcard {
  max-width: 100%;
  display: flex;
  border-radius: 8px;
  flex-direction: column;
  padding: 0rem 1rem;
  align-items: flex-start;
  max-height: 85vh;
  overflow-y: auto;
  margin: 2rem 0.5rem;
}

.detailcard__img img {
  height: auto;
  border-radius: 8px 8px 0 0;
  max-width: 580px;
}

.detailcard__props {
  display: flex;
  justify-content: flex-start;
  gap: 2rem;
  margin-bottom: 0;
}

.detailcard__info {
  padding: 1rem 1rem;
  max-width: 100%;
  overflow-x: hidden;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
  padding-bottom: 0.5rem;
}

.detailcard__img iframe {
  width: 800px;
  height: 450px;

  @media screen and (max-width: 768px) {
    width: 480px;
    height: 360px;
  }
}
