.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
  font-size: 1.5rem;
  color: var(--maintheme);
  background: rgba(0, 0, 0, 0.44);
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  font-weight: 500;
  opacity: 0;
}

.modal.show {
  opacity: 1;
}
